<template>
  <div class="wrap-g" style="height: 100vh;text-align: center">
    <mt-header title="电子签约" fixed  style="background: #fff;color: #1B1B1B;">
      <router-link to="/" slot="left">
        <mt-button icon="back"></mt-button>
      </router-link>
      <!--<mt-button icon="more" slot="right"></mt-button>-->
    </mt-header>
    <img src="../../../../static/img/home/progress2.png" style="width: 40%;height: auto;margin: 70px 0 30px 0">
    <el-form ref="form"   label-width="100px"class="v-form">
      <el-form-item label="姓名：">
        <el-input v-model="ruleForm.name"></el-input>
      </el-form-item>
      <el-form-item label="电话：">
        <el-input v-model="ruleForm.name"></el-input>
      </el-form-item>
      <el-form-item label="推荐人：">
        <el-input v-model="ruleForm.name"></el-input>
      </el-form-item>
      <el-form-item label="讲师级别：" style="text-align: right;margin-right: 15px;" >
        <div @click="handleClick">{{ruleForm.name}}</div>
        <mt-popup v-model="popupVisible" position="bottom" style="width: 100%"   >
          <mt-picker :slots="slots"  v-if="popupVisible"  @change="onValuesChange" value-key="text"></mt-picker>
        </mt-popup>
      </el-form-item>
    </el-form>


    <div style="margin: 20px 0">
      <el-radio v-model="radio" label="1"><span style="color: #707070">我已阅读并同意</span><span style="color: #F39800">《上传课程须知》</span></el-radio>
      <el-button type="primary" round style="width: 70%;margin-top: 15px;" @click="go('/audit')" :disabled="radio==null">完成</el-button>
    </div>

  </div>


</template>

<script>
  import { Header } from 'mint-ui';
  export default {
    data () {
      return {
        popupVisible:false,
        radio: null,
        desc:"协议内容",
        ruleForm:{
          name:"213"
        },
        rules: {
          name: [
            { required: true, message: '请输入活动名称', trigger: 'blur' },
            { min: 3, max: 5, message: '长度在 3 到 5 个字符', trigger: 'blur' }
          ]
        },
        slots: [
          {
            values: [
              {
                text:"123",
                id:"11"
              },
              {
                text:"444",
                id:"55"
              },
            ],
            className: 'slot1',
          }
        ]
      }
    },
    methods:{
      handleClick(){
        this.popupVisible = true
      },
      go(e){
        this.$router.push(e)
      },
      onValuesChange(picker, values) {
        this.ruleForm.name=values[0].id
      }
    },
    mounted(){

    },


  }
</script>


<style scoped>
</style>



